<template>
    <div class="container">
        <div class="row">
            <div class="col-xs-12">
                <v-breadnav>惠鑫贷</v-breadnav>
            </div>
        </div>
        <div class="row loan">
            <div class="col-xs-12">
                <ul class="loan-list" v-if="loanList.length !=0">
                    <li class="loan-item clearfix" v-for="(item,index) in loanList" :key="index">
                        <div class="col-xs-3">
                            <div class="loan-item-img item1">
                                <h3>{{item.financeName}}</h3>
                                <div class="icon">
                                    <img :src="item.icon" alt="" class="img-responsive">
                                </div>
                                <button class="btn" :data-loanid="item.id" @click="toLoanForm(item.id)">立即申请</button>
                            </div>
                        </div>
                        <div class="col-xs-9">
                            <table class="loan-text">
                                <tbody>
                                    <tr>
                                        <td>最低首付比例</td>
                                        <td>最低贷款额</td>
                                    </tr>
                                     <tr>
                                        <td><span class="red">{{item.rate}}</span></td>
                                        <td><span class="red">{{item.moneys}}</span></td>
                                    </tr>
                                    <tr>
                                        <td>贷款期限</td>
                                        <td>申请提交资料及二手车准入标准</td>
                                    </tr>
                                     <tr>
                                        <td><span class="red">{{item.period}}</span></td>
                                        <td><span class="text">{{item.standard}}</span></td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return {
            imgurl1:"static/images/ic_loan1.png",
            imgurl2:"static/images/ic_loan2.png",
            imgurl3:"static/images/ic_loan3.png",
            imgurl4:"static/images/ic_loan4.png",
            loanList:[]
        }
    },
    components:{
        vBreadnav
    },
    methods:{
        getloanList:function(pageNum){
            let _this = this;
            let data = {};
            if(pageNum){
                data.pageNum = pageNum;
            }else{
                data.pageNum = 1;
            }
            data.pageSize = 10;
            data.platForm = "020003";
             $.ajax({
                url:PC_COMMONURL+'/queryFinanceList',
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        console.log(data.data);
                        _this.loanList = data.data.financeList;
                    }
                }
            })
        },
        toLoanForm(id) {
            this.$router.push({
                name: 'loanForm',
                params: {
                id: id
                }
            })
        }
    },
    mounted:function(){
        this.getloanList();
    }
}
</script>



